<template>
  <div>
    <Header />
    <div class="register-page">
      <div class="form">
        <form class="register-form" @submit.prevent="register">
          <input type="text" placeholder="name" v-model="name" />
          <input type="text" placeholder="email address" v-model="email" />
          <input type="password" placeholder="password" v-model="password" />
          <button v-if="!showLoading">create</button>
          <div v-if="showLoading" class="spinner">
            <b-spinner variant="light" label="Spinning"></b-spinner>
          </div>
          <p class="message">
            Already registered?
            <router-link to="/login">Sign In</router-link>
          </p>
        </form>
      </div>
    </div>
  </div>
</template>


<script>
import firebase from "firebase";
import Header from "../layout/Header";

export default {
  name: "Register",
  components: {
    Header
  },
  data() {
    return {
      name: "",
      email: "",
      password: "",
      showLoading: false
    };
  },
  methods: {
    register() {
      this.showLoading = true;
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          () => {
            this.$router.replace({ name: "Dashboard" });
          },
          err => {
            this.showLoading = false;
            alert(err.message);
            console.log("error", err.message);
          }
        );
    }
  }
};
</script>


<style scoped>
.register-page {
  width: 360px;
  padding: 8% 0 0;
  margin: auto;
}
.form {
  position: relative;
  z-index: 1;
  background: #ffffff;
  max-width: 360px;
  margin: 0 auto 100px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.form input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}
.form button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #4caf50;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #ffffff;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}
.spinner {
  background: #4caf50;
  width: 100%;
  border: 0;
  padding: 10px;
  color: #ffffff;
  font-size: 14px;
}
.form button:hover,
.form button:active,
.form button:focus {
  background: #43a047;
}
.form .message {
  margin: 15px 0 0;
  color: #b3b3b3;
  font-size: 12px;
}
.form .message a {
  color: #4caf50;
  text-decoration: none;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 300px;
  margin: 0 auto;
}
.container:before,
.container:after {
  content: "";
  display: block;
  clear: both;
}
.container .info {
  margin: 50px auto;
  text-align: center;
}
.container .info h1 {
  margin: 0 0 15px;
  padding: 0;
  font-size: 36px;
  font-weight: 300;
  color: #1a1a1a;
}
.container .info span {
  color: #4d4d4d;
  font-size: 12px;
}
.container .info span a {
  color: #000000;
  text-decoration: none;
}
.container .info span .fa {
  color: #ef3b3a;
}
</style>
